.button {
  --height: 40px;

  border: none;
  font-family: Arial, sans-serif;
  font-size: 14px;
  font-weight: var(--normalWeight);
  line-height: var(--height);
  outline: none;
  padding: 0 26px;
}

.button img {
  vertical-align: middle;
}

.button:not([disabled]) {
  cursor: pointer;
}

.button--primary {
  background-color: #5e97d1;
  color: #fff;
}

.button--primary:hover {
  background-color: #407fc0;
}

.button--primary[disabled] {
  background-color: #cedff1;
  color: #dde9f5;
}

.button--secondary {
  background-color: #f5f5f5;
  color: #8e939c;
}

.button--secondary:hover {
  background-color: #5e97d1;
  color: #fff;
}

.button--secondary[disabled] {
  background-color: #f9f9f9;
  color: #dedede;
}

.button--rounded {
  border-radius: var(--height);
}

.button--roundedLeft {
  border-radius: var(--height) 0 0 var(--height);
}

.button--roundedRight {
  border-radius: 0 var(--height) var(--height) 0;
}

@media screen and (max-width: 767px) {
  .button {
    --height: 30px;

    padding: 0 17px;
  }
}

.productItem {
  background-color: #fff;
  border-radius: 8px;
  display: flex;
  margin-bottom: 28px;
  overflow: hidden;
}

.productItem:last-child {
  margin-bottom: 0;
}

.productItem__photo {
  align-self: center;
  flex-shrink: 0;
  max-width: 38%;
}

.productItem__container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 38px 55px;
}

.productItem__topContainer {
  display: flex;
  margin-bottom: 16px;
}

.productItem__title {
  flex-grow: 1;
  font-size: 38px;
  font-weight: var(--normalWeight);
  line-height: 44px;
  margin: 0;
}

.productItem__price {
  align-self: flex-end;
  font-size: 21px;
  line-height: 24px;
  margin: 0 0 4px;
}

.productItem__bottomContainer {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.productItem__inventory {
  color: #939393;
  flex-grow: 1;
  font-size: 13px;
  line-height: 15px;
  margin: 0;
}

.productItem__actionsContainer {
  display: flex;
}

.productItem__quantity {
  border: 1px solid #f5f5f5;
  box-sizing: border-box;
  flex-grow: 1;
  font-size: 14px;
  height: 40px;
  outline: none;
  padding: 0 10px;
  text-align: center;
}

.productItem__quantity:focus {
  border-color: #5e97d1;
}

@media screen and (max-width: 1279px) {
  .productItem__price {
    margin: 0 0 2px;
  }

  .productItem__inventory {
    font-size: 12px;
    line-height: 14px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .productItem {
    margin-bottom: 24px;
  }

  .productItem__container {
    padding: 26px 38px;
  }

  .productItem__topContainer {
    margin-bottom: 10px;
  }

  .productItem__title {
    font-size: 28px;
    line-height: 32px;
  }
}

@media screen and (max-width: 767px) {
  .productItem {
    flex-direction: column;
    margin-bottom: 10px;
    width: 294px;
  }

  .productItem__container {
    box-sizing: border-box;
    min-height: 160px;
    padding: 22px 24px;
  }

  .productItem__topContainer {
    margin-bottom: 8px;
  }

  .productItem__photo {
    height: 206px;
    max-width: none;
  }

  .productItem__title {
    font-size: 21px;
    line-height: 24px;
  }

  .productItem__price {
    font-size: 16px;
    line-height: 18px;
  }

  .productItem__quantity {
    height: 30px;
  }
}

.cart__name {
  font-size: 38px;
  font-weight: var(--normalWeight);
  line-height: 32px;
  margin: 0;
}

.cart__divider {
  margin: 24px 0;
}

.cart__totalContainer {
  display: flex;
  padding: 0 38px;
}

.cart__totalLabel,
.cart__totalPrice {
  font-size: 21px;
  line-height: 24px;
  margin: 0 0 24px;
}

.cart__totalLabel {
  color: #939393;
  flex-grow: 1;
}

.cart__checkout {
  width: 100%;
}

.cart__emptyContainer {
  align-items: center;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  padding: 60px 0;
}

.cart__emptyIcon {
  height: 76px;
  margin-bottom: 25px;
  width: 95px;
}

.cart__emptyTip {
  color: #919191;
  font-size: 21px;
  line-height: 28px;
  margin: 0;
  max-width: 260px;
  text-align: center;
}

@media screen and (max-width: 767px) {
  .cart {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 294px;
  }

  .cart__name {
    font-size: 28px;
  }

  .cart__totalContainer {
    padding: 0 13px;
  }

  .cart__emptyIcon {
    height: 57px;
    margin-bottom: 20px;
    width: 72px;
  }

  .cart__emptyTip {
    font-size: 16px;
    line-height: 23px;
    max-width: 200px;
  }
}

.cartModal {
  background-color: rgba(9,9,9,.09);
  box-sizing: border-box;
  cursor: pointer;
  display: grid;
  height: 100%;
  left: 0;
  overflow-y: scroll;
  padding: 20px;
  position: fixed;
  top: 0;
  width: 100%;
}

.cartModal__container {
  align-items: center;
  display: grid;
  height: 100%;
  justify-items: center;
  pointer-events: none;
}

.cartModal__contents {
  background-color: #fff;
  border: 1px solid #e1e1e1;
  border-radius: 14px;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.17);
  box-sizing: border-box;
  cursor: auto;
  max-width: 780px;
  padding: 35px 40px;
  pointer-events: auto;
  position: relative;
  width: 100%;
}

.cartModal__closeIcon {
  --size: 30px;

  cursor: pointer;
  height: var(--size);
  position: absolute;
  right: var(--size);
  top: var(--size);
  width: var(--size);
}

.cartModal .productItem {
  border-radius: 0;
}

.cartModal .productItem__price {
  margin: 0 0 2px;
}

.cartModal .productItem__add,
.cartModal .productItem__remove {
  font-size: 25px;
  line-height: 25px;
}

.cartModal .productItem__add {
  padding-top: 1px;
}

.cartModal .productItem__remove {
  padding-bottom: 4px;
}

@media screen and (min-width: 768px) {
  .cartModal .productItem__container {
    padding: 13px 38px;
  }

  .cartModal .productItem__topContainer {
    margin-bottom: 10px;
  }

  .cartModal .productItem__title {
    font-size: 28px;
    line-height: 32px;
  }

  .cartModal .productItem__inventory {
    font-size: 12px;
    line-height: 14px;
  }
}

@media screen and (max-width: 767px) {
  .cartModal {
    padding: 0;
  }

  .cartModal__contents {
    border: none;
    border-radius: 0;
    box-shadow: none;
    height: 100%;
    padding: 48px 13px 13px;
  }

  .cartModal__closeIcon {
    --size: 20px;
  }

  .cartModal .productItem {
    max-width: none;
  }

  .cartModal .productItem__photo {
    height: auto;
    max-width: 100%;
  }

  .cartModal .productItem__container {
    min-height: 130px;
    padding: 13px;
  }

  .cartModal .productItem__add,
  .cartModal .productItem__remove,
  .cartModal .productItem__quantity {
    width: 100%;
  }

  .cartModal .productItem__totalContainer {
    padding: 0 13px;
  }
}

body {
  --normalWeight: 400;

  background-color: #f4f5f7;
  box-sizing: border-box;
  color: #222;
  font-family: Arial, sans-serif;
  font-weight: var(--normalWeight);
  margin: 0 auto;
  padding: 55px 82px;
}

hr {
  border: none;
  border-top: 1px solid #dadada;
}

.app__header {
  display: flex;
}

.app__storeName {
  flex-grow: 1;
  font-size: 51px;
  font-weight: var(--normalWeight);
  line-height: 44px;
  margin: 0;
}

.app__cart {
  align-self: flex-end;
  cursor: pointer;
  font-size: 14px;
  line-height: 16px;
  margin: 0 0 1px;
}

.app__cartIcon {
  height: 14px;
  margin-right: 8px;
  width: 17px;
  vertical-align: top;
}

.app__divider {
  margin: 32px 0;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  body {
    padding: 47px 49px;
  }

  .app__storeName {
    font-size: 38px;
    line-height: 35px;
  }

  .app__divider {
    margin: 28px 0;
  }
}

@media screen and (max-width: 767px) {
  body {
    padding: 33px 13px;
    width: 320px;
  }

  .app__header {
    flex-direction: column;
  }

  .app__storeName {
    font-size: 28px;
  }

  .app__cart {
    align-self: flex-start;
    margin: 0;
  }

  .app__divider {
    margin: 20px 0;
  }
}

